/* eslint-disable */ 
<template>
  <div class="container">
    <h1>ENCURTADOR</h1>

    <!-- Criar post aqui-->
    <div class="create-post">
      <label for="create-post" class="">Informe o link</label>
      <input
        type="text"
        id="create-post"
        v-model="text"
        placeholder="ENCURTAR URL"
      />
      <button v-on:click="createPost">ENCURTAR!</button>
    </div>

    <!--Mostrar Posts-->
    <hr />
    <p class="error" v-if="error">
      {{ error }}
    </p>

    <div
      class="post"
      v-for="(url, index) in urls"
      v-bind:item="url"
      v-bind:index="index"
      v-bind:key="url._id"
      v-on:dblclick="deletePost(url.id)"
    >
      {{
        `${url.createAt.getDate()}/${url.createAt.getMonth()}/${url.createAt.getFullYear()}`
      }}
      <p class="text">{{ domain }}{{ url.code }}</p>
      <button @click="copiarUrl(domain, url.code)">COPIAR URL</button>
      <p>
        Redireciona para: <br />
        {{ url.url }}
      </p>
      <div class="StatsRow">
        <div class="statsBox">
          <b>
            {{ url.hits }}
          </b>
          <p>Visitas</p>
        </div>
        <div class="statsBox">
          <b>
            {{ url.updatedAt }}
          </b>
          <p>Última Visita</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UrlSerice from "@/service/Urls";

export default {
  name: "PostUrl",
  data() {
    return {
      urls: [],
      error: "",
      text: "",
      domain: "",
    };
  },
  async created() {
    try {
      this.urls = await UrlSerice.getPosts();
      this.domain =
        window.location.protocol + "//" + window.location.host + "/";
    } catch (err) {
      this.err = err.message;
    }
  },
  methods: {
    async createPost() {
      await UrlSerice.insertPost(this.text);
      this.urls = await UrlSerice.getPosts();
    },
    async deletePost(id) {
      await UrlSerice.deletePost(id);
      this.urls = await UrlSerice.getPosts();
    },
    copiarUrl(domain, code) {
      var url = domain + code;
      // Copy the text inside the text field
      navigator.clipboard.writeText(url);

      // Alert the copied text
      alert("Copiado: " + url);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.container {
  max-width: 800px;
  margin: 0 auto;
}

p.error {
  border: 1px solid #ff5b5f;
  background-color: #ffc5c1;
  padding: 10px;
  margin-bottom: 15px;
}

div.post {
  position: relative;
  border: 1px solid #ffa500;
  background-color: #ffbc40;
  padding: 10px 10px 30px 10px;
  margin-bottom: 15px;
}

div.created-at {
  position: absolute;
  top: 0;
  left: 0;
  padding: 5px 15px 5px 15px;
  background-color: darkorange;
  color: white;
  font-size: 13px;
}
p.text {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 0;
}
</style>
