/* eslint-disable */
import axios from 'axios';

const url = '/api/urls/'

class UrlService {
    // Get
    static getOneUrl(linkUrl) {
        return axios.get(url + linkUrl)

    }
    static getPosts() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url)
                const data = res.data;
                resolve(
                    data.map(url => ({
                        ...url,
                        createAt: new Date(url.updatedAt)
                    }))
                )
            } catch (err) {
                reject(err)
            }
        })
    }

    // Create
    static insertPost(text) {
        const post = url + 'new'
        return axios.post(post, {
            text
        })
    }
    // Delete

    static deletePost(id) {
        const del = url + 'delete/' + id
        return axios.delete(del)
    }
}


export default UrlService