/* eslint-disable */ 
<template>
  <div class="container">
    <h1>POSTS</h1>

    <!-- Criar post aqui-->
    <div class="create-post">
      <label for="create-post" class="">Diga o que criar</label>
      <input
        type="text"
        id="create-post"
        v-model="text"
        placeholder="CRIAR ANOTACAO"
      />
      <button v-on:click="createPost">POSTAR!</button>
    </div>

    <!--Mostrar Posts-->
    <hr />
    <p class="error" v-if="error">
      {{ error }}
    </p>
    <div class="posts-container">
      <div
        class="post"
        v-for="(post, index) in posts"
        v-bind:item="post"
        v-bind:index="index"
        v-bind:key="post._id"
        v-on:dblclick="deletePost(post._id)"
      >
        {{
          `${post.createAt.getDate()}/${post.createAt.getMonth()}/${post.createAt.getFullYear()}`
        }}
        <p class="text">{{ post.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from "@/service/Post";

export default {
  name: "PostComponent",
  data() {
    return {
      posts: [],
      error: "",
      text: "",
    };
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch (err) {
      this.err = err.message;
    }
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    },
  },
};
</script>

