/* eslint-disable */

<script>
import UrlSerice from "@/service/Urls";
export default {
  async created() {
    try {
      var domain = window.location.pathname;
      const resultado = await UrlSerice.getOneUrl(domain.substring(1));
      window.location.href = resultado.data.url;
    } catch (err) {
      this.err = err.message;
    }
  },
};
</script>
